<template>
  <div class='mask'>
    <div class='mask-con'>
      <p v-for="(item, _index) in messages" :key="_index" :class="'p' + ++_index">{{item.replace(/__TIMES__/, times)}}</p>
    </div>
  </div>
</template>

<script>
import data from '../config/data.js'

export default {
  props: {
    index: Number,
    times: Number
  },
  data () {
    return {
      sp: data.sp
    }
  },
  computed: {
    messages () {
      return this.sp[this.index].messages
    }
  }
}
</script>

